
import { S } from "./menu_styles";

const items = ["Home", "About","Tech Stack","Propjects","Contacts"];

export const Menu:React.FC = () => {
  return (
    <S.MenuList>
    	{items.map((item, index) => {
			return (
			<S.MenuItem key={index}>
				<S.Link href="#">{item}</S.Link>
			</S.MenuItem>
			);
    	})}
    </S.MenuList>
  );
};
